@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (min-width: 400px) {
  #inputCont {
    height: auto;
    display: flex;
    justify-content: center;
  }
  #inputCont input {
    height: 30px;
    width: 160px;
  }
  #inputCont button {
    height: 30px;
  }
  #inputCont input,
  #inputCont button {
    border-radius: 5px;
    border: 1px solid black;
    transition: ease-in-out 0.25s;
  }
  #addTodoButton {
    background: black;
    color: white;
    font-family: lato, sans-serif;
    transition: var(--transition);
    margin-left: 5px;
  }
  #addTodoButton:hover {
    background: rgb(68, 68, 68);
    cursor: pointer;
  }
  #addTodo {
    background-color: rgba(255, 255, 255, 0.562);
  }
}

@media screen and (min-width: 800px) {
  #inputCont {
    height: auto;
  }
  #inputCont input {
    height: 50px;
    width: 200px;
  }
  #inputCont button {
    height: 50px;
    width: 75px;
  }
}
@media screen and (min-width: 1000px) {
  #inputCont input {
    width: 230px;
  }
  #inputCont button {
    width: 85px;
  }
}

#TodosMain {
  height: 100%;
  background-image: linear-gradient(
      rgba(173, 172, 172, 0.479),
      rgba(173, 172, 172, 0.479)
    ),
    url(/static/media/TodosBackground.454f3d4d.jpg);
  background-size: cover;
  background-position: center;
}

@media screen and (min-width: 400px) {
  #todosHeader {
    height: auto;
  }
  #todosLink {
    color: white;
    text-decoration: none;
    font-family: lato, sans-serif;
    font-size: 20px;
    margin-right: 10px;
    transition: var(--transition);
    background-color: var(--accent);
    border-radius: 8px;
  }
  #todosHomeCont img {
    height: 30px;
    transition: var(--transition);
  }
  #todosLink:hover {
    background-color: var(--accentHover);
    color: black;
  }
  ol {
    margin-top: 30px;
    height: auto;
    width: auto;
    transition: var(--transition);
  }
  #todoCont {
    height: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    flex-wrap: wrap;
    border-bottom: 1px solid black;
    transition: var(--transition);
    background: rgba(245, 243, 243, 0.671);
  }
  #todoCont li {
    margin-left: 10px;
    font-weight: 600;
  }
  #todoCont button {
    margin-top: 15px;
    margin-right: 15px;
  }
  #buttonCont .Delete {
    background-image: url(/static/media/remove.dc8a2982.png);
    background-size: cover;
    width: 30px;
    height: 30px;
    border-radius: 50px;
    border: none;
    transition: var(--transition);
  }
  #buttonCont .Delete:hover {
    cursor: pointer;
    background: linear-gradient(rgba(114, 2, 2, 0.205), rgba(114, 2, 2, 0.205)),
      url(/static/media/remove.dc8a2982.png);
    background-size: cover;
  }
  #buttonCont .completed {
    background-image: url(/static/media/checkmark.90d3233b.png);
    background-size: cover;
    width: 30px;
    height: 30px;
    border-radius: 50px;
    border: none;
    transition: var(--transition);
  }
  #buttonCont .completed:hover {
    cursor: pointer;
    background: linear-gradient(rgba(3, 110, 3, 0.171), rgba(3, 110, 3, 0.171)),
      url(/static/media/checkmark.90d3233b.png);
    background-size: cover;
  }
}

@media screen and (min-width: 600px) {
  ol {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }
  #todoCont {
    flex-direction: row;
    justify-content: space-between;
  }
  #todoCont li {
    margin-left: 10px;
    font-size: 16px;
  }
  #buttonCont .Delete,
  #buttonCont .completed {
    height: 35px;
    width: 35px;
  }
}

@media screen and (min-width: 800px) {
  #todosHomeCont img {
    height: 40px;
  }
  #todosLink {
    font-size: 25px;
  }
  ol {
    width: 80%;
  }
}

@media screen and (min-width: 1000px) {
  #todosHomeCont img {
    height: 45px;
  }
  #todosLink {
    font-size: 30px;
  }
  ol {
    width: 58%;
  }
}
@media screen and (min-width: 1400px) {
  ol {
    width: 40%;
  }
}

:root {
  --accent: #ea928da6;
  --accentHover: #c57a76;
  --dark: #304659;
  --dark80: rgba(48, 70, 89, 0.8);
  --dark60: rgba(48, 70, 89, 0.6);
  --light: #f5f9fc;
  --transition: all 0.3s ease 0s;
  --transitionUnderLine: all 0.3s ease-in-out 0s;
}
* {
  overflow: auto;
  height: 100%;
}

section {
  background: linear-gradient(rgba(48, 70, 89, 0.6), rgba(48, 70, 89, 0.6)),
    url(/static/media/xps-DYAf-8UTFN8-unsplash.3d0bda57.jpg);
  background: linear-gradient(var(--dark60), var(--dark60)),
    url(/static/media/xps-DYAf-8UTFN8-unsplash.3d0bda57.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

header {
  background: transparent;
  margin-top: 0;
  font-family: lato, sans-serif;
  align-items: center;
  display: flex;
  justify-content: space-between;
  height: 50px;
}
h1 {
  background-color: transparent;
  color: white;
}
#TodosIcon {
  height: 90px;
}
#GithubIcon,
#ProfileIcon {
  height: 50px;
}
#Links {
  background-color: transparent;
}
.LinkCont,
a,
img,
.ProjectCont {
  background-color: transparent;
}
@media screen and (min-width: 400px) {
  header {
    margin-top: 5px;
  }
  h1 {
    transition: all 0.3s ease 0s;
    transition: var(--transition);
  }
  #GithubIcon,
  #ProfileIcon {
    height: 30px;
    transition: all 0.3s ease 0s;
    transition: var(--transition);
  }
  #GithubLink,
  #ProfileLink {
    color: white;
    font-family: lato, sans-serif;
    text-decoration: none;
    transition: all 0.3s ease 0s;
    transition: var(--transition);
    font-size: 20px;
    background-color: #ea928da6;
    background-color: var(--accent);
    border-radius: 8px;
  }
  #GithubLink:hover,
  #ProfileLink:hover,
  #TodosLink:hover {
    background-color: #c57a76;
    background-color: var(--accentHover);
    color: black;
    cursor: pointer;
  }
  #ProfileLink,
  #GithubLink {
    margin-right: 10px;
    transition: all 0.3s ease 0s;
    transition: var(--transition);
  }
  .ProjectCont {
    display: flex;
    justify-content: center;
    margin-top: 10em;
    height: 100px;
    align-items: center;
  }
  #TodosLink {
    height: auto;
    font-size: 25px;
    color: white;
    text-decoration: none;
    transition: all 0.3s ease 0s;
    transition: var(--transition);
    background-color: #ea928da6;
    background-color: var(--accent);
    border-radius: 8px;
    font-family: lato, sans-serif;
  }
}

@media screen and (min-width: 700px) {
  h1 {
    margin-left: 10px;
  }
  #GithubIcon,
  #ProfileIcon {
    height: 40px;
  }
  #ProfileLink,
  #GithubLink {
    margin-right: 15px;
    font-size: 25px;
  }
  #TodosLink {
    font-size: 30px;
  }
}

@media screen and (min-width: 1000px) {
}

